import { Provider } from '@ethersproject/abstract-provider';
import { BigNumberish } from '@ethersproject/bignumber';
import { Contract } from '@ethersproject/contracts';
import { Transaction } from '@ethersproject/transactions';
import { Wallet } from '@ethersproject/wallet';
import { default as WethAbi } from './abi/Weth.json';
import { WETH } from './utils/constants';
import { ChainId } from '.';

/**
 * Function to wrap a specific amount of ether for the specified wallet
 * (MAINNET ONLY!)
 * @param {BigNumberish} amount
 * @param {Wallet} wallet
 * @returns {Promise<Transaction>}
 */
export const wrapEth = async (
  amount: BigNumberish,
  wallet: Wallet
): Promise<Transaction> => {
  const instance = new Contract(
    WETH[ChainId.mainnet],
    JSON.stringify(WethAbi),
    wallet
  );

  return instance.deposit({
    value: amount,
  });
};

/**
 * Function to unwrap a specific amount of ether for the specified wallet
 * (MAINNET ONLY!)
 * @param {BigNumberish} amount
 * @param {Wallet} wallet
 * @returns {Promise<Transaction>}
 */
export const unwrapWeth = async (
  amount: BigNumberish,
  wallet: Wallet
): Promise<Transaction> => {
  const instance = new Contract(
    WETH[ChainId.mainnet],
    JSON.stringify(WethAbi),
    wallet
  );

  return instance.withdraw(amount);
};

/**
 * Function that returns a pointer to the smart contract
 * function that wraps or unwraps, to be used by estimateGas calls
 * (MAINNET ONLY!)
 * @param {string} name
 * @param {Provider} provider]
 * @returns {Promise<Transaction>}
 */
export const geWethMethod = (name: string, provider: Provider): any => {
  const instance = new Contract(
    WETH[ChainId.mainnet],
    JSON.stringify(WethAbi),
    provider
  );
  return instance.estimateGas[name];
};
